  background-position: right top;
  background-repeat: no-repeat;
  background-size: contain;
  position: relative;
  padding: 60px 20px 0; }
  #news #news-wrapper {
    border: 1px solid #E0E0E0;
    border-radius: 4px;
    padding: 40px 30px; }
  #news .major h2 {
    color: #555;
    font-size: 28px; }
  #news .news:nth-of-type(1) .date,
  #news .news:nth-of-type(5) .date {
    background-color: #3096b2; }
  #news .news:nth-of-type(2) .date,
  #news .news:nth-of-type(6) .date {
    background-color: #34459e; }
  #news .news:nth-of-type(3) .date,
  #news .news:nth-of-type(7) .date {
    background-color: #ce4d40; }
  #news .date {
    background-color: #575757;
    text-align: center;
    font-size: 16px;
    color: white;
    line-height: 1;
    padding: 8px 0; }
    #news .date span {
      margin: 0 5px; }
  #news .news-container .news {
    padding: 16px 0; }
    #news .news-container .news .news-title, #news .news-container .news .news-paragraph {
      color: #575757;
      margin: 0;
      line-height: 1.5; }
    #news .news-container .news .news-title {
      text-align: center;
      font-size: 20px;
      line-height: 2.5;
      font-weight: bold;
      color: #222; }
    #news .news-container .news .news-paragraph {
      font-size: 16px; }
  #news .news-container .news + .news {
    border-top: 1px solid #ddd; }
  #news #news-line {
    position: absolute;
    top: 0;
    left: 50%;
    margin: 0 1em 0 0; }
    #news #news-line:after {
      background: #E0E0E0;
      content: '';
      display: inline-block;
      height: 60px;
      width: 1px; }

/* About */
