    #about #about-jsdc .major {
      text-align: center; }
      #about #about-jsdc .major h2 {
        color: #555;
        font-size: 28px; }
  #about p {
    color: #555;
    text-align: justify;
    margin: 0 0 30px;
    font-size: 16px;
    line-height: 2; }
  #about .row + .row {
    padding-top: 60px; }
    #about .row + .row .major h2 {
