      color: white; }
  #speaker .major {
    text-align: center;
    padding-top: 60px; }
    #speaker .major h2 {
      color: #555;
      font-size: 28px; }
  #speaker #speaker-line {
    position: absolute;
    top: 0;
    left: 50%; }
    #speaker #speaker-line:after {
